'use strict';

var template = require('./request.html');

new Vue({
    el: "#app",
    template: template,
    methods: {
        localReq: function localReq() {
            $.getJSON('http://192.168.1.24:8888/admin/login', function (res) {
                console.log(res);
            });
        },
        serverReq: function serverReq() {
            $.ajax({
                type: 'post',
                url: '/admin/login',
                data: {
                    aaa: 'aaa',
                    path: '/admin/login'
                },
                success: function success(data) {
                    console.log('success:' + data);
                },
                error: function error(err) {
                    console.log('err:' + err);
                }
            });
        }
    }
});


